@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
  height: 100vh;
  font-family: "Poppins", sans-serif;
  background-color: #f5f5f5;
}

li {
  list-style-type: none;
}

/****/

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader_text {
  font-size: 18px;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 90px 0 70px 0;
}

.movie {
  display: flex;
  background-color: whitesmoke;
  width: 500px;
  max-height: 600px;
  margin-bottom: 70px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 11px 11px 22px #e1e1e1, -11px -11px 22px #ffffff;
}

.movie_poster {
  display: block;
  flex-shrink: 0;
  position: relative;
  top: -40px;
  width: 100px;
  height: 150px;
  box-shadow: 0px 6px 14px 5px rgba(142, 142, 142, 0.4);
}

.movie_data {
  margin-left: 20px;
}

.movie_title {
  line-height: 1.1;
  font-size: 18px;
  color: #5c5b57;
}

.movie_year {
  font-weight: 400;
}

.movie_genres {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 10px;
  padding: 0;
}

.movie_genres > li:not(:last-of-type) {
  margin-right: 10px;
}

.movie_year,
.genres_genre {
  font-size: 11px;
}

.movie_summary,
.movie_year,
.movie_genres {
  color: #aaa69d;
}

.movie_summary {
  font-size: 13px;
}

/**In desktop**/

.movies {
  max-width: 1240px;
  justify-content: space-around;
}

.movie:last-of-type,
.movie:nth-of-type(19) {
  margin-bottom: 0;
}

/**other devices**/

@media screen and (max-width: 1016px) {
  .movies {
    justify-content: center;
  }

  .movie:nth-of-type(19) {
    margin-bottom: 70px;
  }
}

@media screen and (max-width: 540px) {
  .movie {
    width: 80%;
  }

  .movie_poster {
    width: 80px;
    height: 120px;
  }
}

